var filter = function (text, length, clamp) {
  clamp = clamp || '...'
  return text.length > length ? text.slice(0, length) + clamp : text
}

Vue.filter('truncate', filter)
Vue.use(VuesticPlugin)
Vue.use(YmapPlugin)
Vue.use(VueClipboard)
Vue.use(TreeView)
Vue.use(Antd)

Vue.component('reload', ReloadBtn)

var retrievedObject = localStorage.getItem('themesColors')
if (retrievedObject) {
  Vue.use(ColorThemePlugin, JSON.parse(retrievedObject))
} else {
  Vue.use(ColorThemePlugin, {
    // override colors here.
    primary: '#ff512f',
    secondary: '#000000',
    success: '#40e583',
    info: '#2c82e0',
    danger: '#e34b4a',
    warning: '#ffc200',
    gray: '#828080',
    dark: '#34495e',
    navtext: '#DAD9D9',

  })
  localStorage.setItem('themesColors', JSON.stringify({
    // override colors here.
    primary: '#ff512f',
    secondary: '#000000',
    success: '#40e583',
    info: '#2c82e0',
    danger: '#e34b4a',
    warning: '#ffc200',
    gray: '#828080',
    dark: '#34495e',
    navtext: '#DAD9D9',

  }))
}

router.beforeEach((to, from, next) => {
  const publicPages = ['/auth/login', '/auth/signup', '/auth/recover-password']
  const authRequired = !publicPages.includes(to.path)
  const loggedIn = localStorage.getItem('user')
  const mustlogin = localStorage.getItem('mustlogin')

  // trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next('/auth/login')
  } else if (authRequired && loggedIn && mustlogin) {
    next('/auth/recover-password')
  } else {
    next()
  }
})

router.afterEach((to, from) => {
  store.commit('setLoading', false)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
})
