import Vue from 'vue'
import Vuex from 'vuex'
import VuexI18n from 'vuex-i18n' // load vuex i18n module
import app from './modules/app'
import AuthService from '../services/AuthService/AuthService'
import * as getters from './getters'

Vue.use(Vuex)

const user = JSON.parse(localStorage.getItem('user'))
const initialState = user
  ? {
    vars: {
      fK_Branchs: 1,
      fK_Company_Activity: 1,
      fK_Employees: user.id,
      fK_stock: 3,
      max_DisscountPercntage: 3,
    },
    consts: {
      invPrintWidth: user.PrintWidth == null ? 21 : user.PrintWidth,
      fK_Safes: 1,
      fK_Pay_Type: 1,
      fK_Inv_PayType: 1,
      fK_Inv_type_sell: 1,
      fK_Inv_type_buy: 2,
      fK_Inv_type_rsell: 3,
      fK_Inv_type_rbuy: 4,
      fK_Customer_Type_customer: 1,
      fK_Customer_Type_mord: 2,
      fK_Restrictions_Type: 7,
      fK_Restrictions_State: 2,
      fK_Pay_Type_cash: 1,
      fK_Pay_Type_Check: 2,
      fK_sand_recive_Type: 1,
      fK_sand_pay_Type: 2,
      fK_Tr_Stock_Type: 5,
      fK_Tr_Stock_Type_inorder: 3,
      fK_Tr_Stock_Type_inorder_Delivery: 9,
      fK_Tr_Stock_Type_outorder: 4,
      fK_Tr_Stock_Type_outorder_Delivery: 10,
    },
    status: {
      loggedIn: true,
      mustChangePass: false,
    },
    user,
  }
  : {
    vars: null,
    consts: {
      invPrintWidth: 21,
      fK_Safes: 1,
      fK_Pay_Type: 1,
      fK_Inv_PayType: 1,
      fK_Inv_type_sell: 1,
      fK_Inv_type: 2,
      fK_Inv_type_rsell: 3,
      fK_Inv_type_rbuy: 4,
      fK_Customer_Type_customer: 1,
      fK_Customer_Type_mord: 2,
      fK_Restrictions_Type: 7,
      fK_Restrictions_State: 2,
      fK_Pay_Type_cash: 1,
      fK_Pay_Type_Check: 2,
      fK_sand_recive_Type: 1,
      fK_sand_pay_Type: 2,
      fK_Tr_Stock_Type: 5,
      fK_Tr_Stock_Type_inorder: 3,
      fK_Tr_Stock_Type_inorder_Delivery: 9,
      fK_Tr_Stock_Type_outorder: 4,
      fK_Tr_Stock_Type_outorder_Delivery: 10,
    },
    status: {
      loggedIn: false,
      mustChangePass: false,
    },
    user: null,
  }
const store = new Vuex.Store({
  strict: true, // process.env.NODE_ENV !== 'production',
  getters,
  modules: {
    app,
  },
  state: initialState,
  actions: {
    login ({ commit }, user) {
      commit('loginSuccess', user)
    },
    chngePass ({ commit }) {
      commit('chngePassSuccess')
    },
    logout ({ commit }) {
      AuthService.logout()
      commit('logout')
    },

  },
  mutations: {
    loginSuccess (state, user) {
      state.status.loggedIn = true
      state.status.mustChangePass = false
      console.log(user)
      state.user = user
    },
    chngePassSuccess (state) {
      state.status.mustChangePass = false
    },
    loginFailure (state) {
      state.status.loggedIn = false
      state.status.mustChangePass = false
      state.user = null
    },
    logout (state) {
      state.status.loggedIn = false
      state.status.mustChangePass = false
      state.user = null
    },
  },
})

Vue.use(VuexI18n.plugin, store)

export default store
